const formatRelativeLocale = {
  lastWeek: "'geen hafta' eeee 'saat' p",
  yesterday: "'dn saat' p",
  today: "'bugn saat' p",
  tomorrow: "'yarn saat' p",
  nextWeek: "eeee 'saat' p",
  other: "P",
};

export const formatRelative = (token, _date, _baseDate, _options) =>
  formatRelativeLocale[token];
